<template>
		<div class="tradingRecord qrcode">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a ></a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p v-if="isTuijian"></p>
				<p v-else>!</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo"  class="cardImg">
				</div>
				<p v-if="isTuijian"></p>
				<p v-else></p>
				<!-- <p class="qrcodeButton">
					<textarea  readonly="readonly" id="shareId"  :value="shareUrl"  class="urlhide"></textarea>
					<button   @click="copyLink" class="btn" data-clipboard-target="#shareId"></button >
				</p> -->
			</div>
			<!-- <div class="overlayer" @touchmove.prevent  v-show="noCopy">
				<div class="offerPayWindow">
					<div  class="offerPayWindowCom offerCopyCom">
						<div v-show="isCopy" class="nozhici">
							<p>:</p>
							<p @click="selectText('shareUrl')" id="shareUrl">{{shareUrl}}</p>
							<textarea readonly="readonly" @click="selectText('shareUrl')" class="urlshow" id="shareUrl" :value="shareUrl"></textarea>
						</div>
						<div class="offerPayButo">
							<p @click="sureCopy" class="sureunde"></p>
						</div>
					</div>
				</div>
			</div> -->
			
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
import axios from 'axios';
import Clipboard from 'clipboard';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	currUrl:'',
		    	urlCardNo:'',
		    	wxshareData:[],
		    	isCopy:false,
		    	noCopy:false,
		    	shareUrl:'',
		    	tuijianCode:'',
		    	isTuijian:true,
		    	share:{
		    		urlShare:'',
			    	shareTitle:'!',
			    	shareDesc:'',
			    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
		    	}
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={},ddd={};
				_this.currUrl=window.location.href;
				window.location.href=_this.currUrl;
				_this.busId= _this.$route.params.busId;
				_this.tuijianCode= _this.$route.query.tuijianCode;
				// _this.isTuijian= _this.$route.query.isTuijian;
				// _this.url=_this.currUrl.split("isTuijian");
				// _this.isTuijian=_this.url[1].split('=')[1];
				// _this.share.urlShare=_this.url[0]+'isTuijian=0';
				// _this.isTuijian=(_this.isTuijian==1)?true:false;
				// console.log('urlShare',_this.urlShare)
				// console.log('tuijianCode',_this.tuijianCode)
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				json["systemCode"]=_this.tuijianCode;
				_this.share.urlShare=_this.currUrl;
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId+"&systemCode="+_this.tuijianCode;
				Lib.M.ajax({
			      	'url': memberAPI.activeAPI.judgeTuijian_POST,
			      	'type': 'POST',
			      	'data': {
			        	"json": json
			      	},
      				'success': function(data) {
      					if(data.code==0){
      						_this.isTuijian=data.data.tuijianCode;
      						_this.isTuijian=(_this.isTuijian==0)?true:false;
      					}
      					Lib.M.ajax({
					      	'url': memberAPI.activeAPI.wxshareCard_POST,
					      	'type': 'POST',
					      	'data': {
					        	"json": json
					      	},
		      				'success': function(data) {
		      					_this.wxshareData=data.data;
		      				},
		      				'error':function(data){
		      					// alert(data)
		      				}
		      			})
      					
      				}
      			})
				
			},
			copyLink(){
				var _this=this;
				var clipboard = new Clipboard('.btn')
			    clipboard.on('success', e => {
			    	_this.isOfferPayOther=false;
			          Lib.M.tipsMsg('',3000);
			      // 
			      clipboard.destroy()
			    })
			    clipboard.on('error', e => {
			      _this.isOfferPayOther=false;
			     _this.noCopy=true;
			      _this.isCopy=true;
			      // 
			      clipboard.destroy()
			    })
			},
			sureCopy(){
				var _this=this;
				_this.noCopy=false;
			},
			selectText() {
			     var NumClip=document.getElementById("shareUrl");
	            var NValue=NumClip.value,startIndex=0;
	            console.log(NValue)
	            var valueLength = NValue.length;
			    if (NumClip.setSelectionRange) {
	                NumClip.setSelectionRange(startIndex, valueLength);
	            } else if (NumClip.createTextRange) {
	                var range = NumClip.createTextRange();
	                range.collapse(true);
	                range.moveStart('character', startIndex);
	                range.moveEnd('character', valueLength - startIndex);
	                range.select();
	            }
	            NumClip.focus();
	            try{
				    if(document.execCommand('copy', false, null)){
				       Lib.M.tipsMsg('',3000);
				    } else{
				        Lib.M.tipsMsg('',3000);
				    }
				} catch(err){
				    Lib.M.tipsMsg(err,3000);
				}
			}
		},
	    mounted:function(){
	    	var _this=this;
	    	_this.loadData();
		},

		watch:{
			wxshareData(newValue, oldValue){
				var _this=this;
				alert(JSON.stringify(newValue))
				alert(JSON.stringify(_this.share))
				// alert(newValue.signature);
				wx.config({
		            debug: false, // ,apialertpclogpc
		            appId: newValue.appid, // 
		            timestamp: newValue.timestamp, // 
		            nonceStr: newValue.nonce_str, // 
		            signature: newValue.signature, // 1
		            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // JSJS2
		        });
				wx.ready(function() {
		            //
		            wx.onMenuShareAppMessage({
			            title: _this.share.shareTitle, // 
			            desc: _this.share.shareDesc, // 
			            link: _this.share.urlShare, // 
			            imgUrl: _this.share.shareImg, // 
			            success: function() {
			                // 
			                Lib.M.tipsMsg('')
			            },
			            cancel: function() {
			                // 
			                 Lib.M.tipsMsg('')
			            },
			            error: function(res) {
			                // alert(res)
			            }
		            });
		            //
		            wx.onMenuShareTimeline({
		             	title: _this.share.shareTitle, // 
		             	desc: _this.share.shareDesc, // 
		              	link: _this.share.urlShare, // 
		              	imgUrl: _this.share.shareImg, // 
		              	success: function() {
		                // 
		              	},
		              	cancel: function() {
		                // 
		              	}
		            });
		        });
			}
		}
		
	}
</script>
<style scope>

</style>


